.userWrapper {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    padding-top: 20px;
}

.pageTitle {
    text-align: center;
    font-size: 32px;
}

.userCard {
    display: flex;
    justify-content: space-between;
    margin: 0px 10px 10px 10px;
    padding: 10px;
    border-radius: 10px;
    background-color: #d5ee9b;
} 

.wrapperAvatar {
    flex-basis: 10%;
    align-self: center;
    text-align: center;
}

.user_photo {
    border-radius: 50%;
    width: 50px;
    height: 50px;
}

.userDesc {
    flex-basis: 80%;
}

.fullName {
    color: #527571;
    margin: 0;
} 

.status {
    font-size: 14px;
    color: grey;
}

.status_icon {
    width: 15px;
    margin-left: 5px;
    margin-right: 5px;
    transform: translateY(3px);
}

.location {
    font-size: 16px;
}

.location_icon {
    width: 20px;
    transform: translateY(3px);
}

.country {
    margin-right: 5px;
}

.city {

}

.followWrap {
    align-self: center;
    text-align: center;
    flex-basis: 10%;
}

.button {
    vertical-align: middle;
    border: 1px solid #4eb6b7;;
    border-radius: 5%;
    box-shadow: 0px 3px 10px #4eb5b785;
    height: 30px;
    cursor: pointer;
    background-color: white;
    color: white;
}

.buttonUnfollow {
    color: #4eb6b7;
}

.buttonFollow {
    background-color: #4eb6b7;
}